
.app-view {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  background-color: #f8f9fa;
  position: absolute;
  top: 0;
  left: 0;
}

.app-header {
  background: linear-gradient(135deg, #ffffff 0, #fafbfc 100%);
  padding: 35px 16px 12px 16px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #f0f0f0;
  flex-shrink: 0;
  backdrop-filter: blur(10px);
  position: relative;

  .title {
    font-size: 1.3rem;
    font-weight: 700;
    display: flex;
    align-items: center;
    gap: 8px;
    color: #2c2c2c;

    i {
      cursor: pointer;
      transition: all 0.25s cubic-bezier(0.34, 1.56, 0.64, 1);
      padding: 4px;
      border-radius: 6px;

      &:hover {
        background-color: #fff3cc;
        transform: scale(1.1) rotate(-5deg);
        box-shadow: 0 1px 2px rgba(0, 0, 0, 0.05);
      }
    }
  }
}

.app-content {
  flex-grow: 1;
  overflow-y: auto;
  padding: 0;
  scrollbar-width: none;
  -ms-overflow-style: none;

  &::-webkit-scrollbar {
    display: none;
  }
}

.shop-header-card {
  background: linear-gradient(135deg, #ffffff 0, #fafbfc 100%);
  border-radius: 16px;
  padding: 16px;
  margin-bottom: 12px;
  text-align: center;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.06);
  border: 1px solid #f8f8f8;
  position: relative;
  overflow: hidden;

  &::before {
    content: '';
    position: absolute;
    top: -50%;
    left: -50%;
    width: 200%;
    height: 200%;
    background: radial-gradient(circle, rgba(255, 195, 0, 0.05) 0, transparent 70%);
    animation: pulse 4s ease-in-out infinite;
  }

  .shop-avatar-text {
    width: 70px;
    height: 70px;
    border-radius: 50%;
    margin: 0 auto 8px;
    border: 4px solid #ffc300;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.4rem;
    font-weight: 700;
    background: linear-gradient(135deg, #fff9e6 0, #fffbf0 100%);
    color: #2c2c2c;
    box-shadow:
      0 4px 12px rgba(255, 195, 0, 0.3),
      0 0 0 3px #ffffff inset;
  }

  .shop-name {
    font-size: 1.25rem;
    font-weight: 800;
    margin-bottom: 4px;
    color: #2c2c2c;
    text-shadow: 0 1px 2px rgba(0, 0, 0, 0.05);
  }

  .shop-slogan {
    font-size: 0.85rem;
    color: #999999;
    margin-top: 4px;
    line-height: 1.4;
  }
}

.list-section {
  background: linear-gradient(135deg, #ffffff 0, #fafbfc 100%);
  padding: 16px;
  border-radius: 16px;
  margin-bottom: 16px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.06);
  border: 1px solid #f8f8f8;
  position: relative;
  overflow: hidden;

  .section-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 16px;

    h3 {
      margin: 0;
      font-size: 1.15rem;
      color: #2c2c2c;
      font-weight: 700;
      display: flex;
      align-items: center;
      gap: 12px;

      &::before {
        content: '';
        display: block;
        width: 4px;
        height: 18px;
        background: linear-gradient(135deg, #ffc300 0, #ff6b35 100%);
        border-radius: 6px;
      }
    }
  }
}

.package-list {
  display: grid;
  grid-template-columns: 1fr;
  gap: 12px;
}

.package-card {
  background: linear-gradient(135deg, #ffffff 0, #fafbfc 100%);
  border-radius: 16px;
  padding: 16px;
  display: flex;
  align-items: center;
  gap: 12px;
  transition: all 0.25s cubic-bezier(0.25, 0.46, 0.45, 0.94);
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.06);
  cursor: pointer;
  border: 1px solid #f8f8f8;
  position: relative;
  overflow: hidden;

  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100%;
    background: linear-gradient(90deg, transparent, rgba(255, 195, 0, 0.05), transparent);
    transition: left 0.35s cubic-bezier(0.25, 0.46, 0.45, 0.94);
  }

  &:hover {
    transform: translateY(-6px) scale(1.02);
    box-shadow:
      0 8px 25px rgba(0, 0, 0, 0.12),
      0 0 0 1px #fff3cc inset;
    border-color: #ffc300;

    &::before {
      left: 100%;
    }
  }

  &:active {
    transform: translateY(-2px) scale(1.01);
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.07);
  }

  .avatar-text {
    width: 64px;
    height: 64px;
    border-radius: 16px;
    flex-shrink: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.8rem;
    font-weight: 700;
    color: #2c2c2c;
    background: linear-gradient(135deg, #fff9e6 0, #fffbf0 100%);
    text-align: center;
    line-height: 1.2;
    box-shadow:
      0 4px 6px rgba(0, 0, 0, 0.07),
      0 0 0 3px #ffffff inset;
    border: 2px solid #fff3cc;
    transition: all 0.25s cubic-bezier(0.25, 0.46, 0.45, 0.94);
    position: relative;
    overflow: hidden;

    &::before {
      content: '';
      position: absolute;
      top: -50%;
      left: -50%;
      width: 200%;
      height: 200%;
      background: radial-gradient(circle, rgba(255, 195, 0, 0.1) 0, transparent 70%);
      transform: scale(0);
      transition: transform 0.25s cubic-bezier(0.25, 0.46, 0.45, 0.94);
    }

    &:hover::before {
      transform: scale(1);
    }
  }

  .info {
    flex-grow: 1;
    min-width: 0;
  }

  .name {
    font-weight: 600;
    margin-bottom: 5px;
    color: #2c2c2c;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }

  .desc {
    display: flex;
    flex-wrap: wrap;
    gap: 5px;
    margin-top: 4px;
    min-height: 34px;
    align-items: center;
  }

  .package-tag {
    background: linear-gradient(135deg, #fff7e6 0, #ffeacc 100%);
    color: #d98600;
    font-size: 0.75rem;
    padding: 5px 12px;
    border-radius: 50px;
    border: 1px solid #ffd699;
    font-weight: 600;
    transition: all 0.25s cubic-bezier(0.25, 0.46, 0.45, 0.94);
    position: relative;
    overflow: hidden;
    box-shadow: 0 1px 3px rgba(217, 134, 0, 0.15);

    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: -100%;
      width: 100%;
      height: 100%;
      background: linear-gradient(90deg, transparent, rgba(217, 134, 0, 0.1), transparent);
      transition: left 0.35s cubic-bezier(0.25, 0.46, 0.45, 0.94);
    }

    &:hover {
      transform: translateY(-1px);
      box-shadow: 0 1px 2px rgba(0, 0, 0, 0.05);
      border-color: #d98600;

      &::before {
        left: 100%;
      }
    }
  }
}

.nav-bar {
  display: flex;
  border-top: 1px solid #f0f0f0;
  background: linear-gradient(135deg, #ffffff 0, #fafbfc 100%);
  padding: 8px 0;
  flex-shrink: 0;
  backdrop-filter: blur(10px);
  position: relative;

  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 1px;
    background: linear-gradient(90deg, transparent, #f0f0f0, transparent);
  }

  .nav-item {
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    color: #666666;
    font-size: 0.8rem;
    padding: 4px 0;
    cursor: pointer;
    transition: all 0.25s cubic-bezier(0.25, 0.46, 0.45, 0.94);
    position: relative;
    border-radius: 8px;
    margin: 0 4px;

    &.active {
      color: #2c2c2c;

      i {
        color: #ffc300;
        transform: scale(1.15) translateY(-2px);
      }
    }

    &:hover:not(.active) {
      color: #333333;
      transform: translateY(-1px);
    }

    i {
      font-size: 1.25rem;
      margin-bottom: 4px;
      transition: all 0.25s cubic-bezier(0.34, 1.56, 0.64, 1);
    }
  }
}

.empty-state {
  text-align: center;
  color: #999999;
  padding: 40px 20px;
  line-height: 1.6;
}

@keyframes pulse {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.05);
  }
  100% {
    transform: scale(1);
  }
}

// 深色模式
body.dark-mode {
  .app-view {
    background-color: #1a1a1a;
  }

  .app-header {
    background: linear-gradient(135deg, #2a2a2a 0, #1f1f1f 100%);
    border-bottom-color: #333;

    .title {
      color: #e0e0e0;
    }
  }

  .shop-header-card {
    background: linear-gradient(135deg, #2a2a2a 0, #1f1f1f 100%);
    border-color: #333;

    .shop-name {
      color: #e0e0e0;
    }

    .shop-slogan {
      color: #999999;
    }
  }

  .list-section {
    background: linear-gradient(135deg, #2a2a2a 0, #1f1f1f 100%);
    border-color: #333;

    .section-header h3 {
      color: #e0e0e0;
    }
  }

  .package-card {
    background: linear-gradient(135deg, #2a2a2a 0, #1f1f1f 100%);
    border-color: #333;

    .name {
      color: #e0e0e0;
    }

    .package-tag {
      background: linear-gradient(135deg, #333 0, #2a2a2a 100%);
      color: #ffc300;
      border-color: #ffc300;
    }
  }

  .nav-bar {
    background: linear-gradient(135deg, #2a2a2a 0, #1f1f1f 100%);
    border-top-color: #333;
  }
}
