
.app-view {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  background-color: #f8f9fa;
  position: absolute;
  top: 0;
  left: 0;
}

.app-header {
  background: linear-gradient(135deg, #ffffff 0, #fafbfc 100%);
  padding: 35px 16px 12px 16px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #f0f0f0;
  flex-shrink: 0;
  backdrop-filter: blur(10px);
  position: relative;

  .title {
    font-size: 1.3rem;
    font-weight: 700;
    display: flex;
    align-items: center;
    gap: 8px;
    color: #2c2c2c;

    i {
      cursor: pointer;
      transition: all 0.25s cubic-bezier(0.34, 1.56, 0.64, 1);
      padding: 4px;
      border-radius: 6px;

      &:hover {
        background-color: #fff3cc;
        transform: scale(1.1) rotate(-5deg);
        box-shadow: 0 1px 2px rgba(0, 0, 0, 0.05);
      }
    }
  }
}

.app-content {
  flex-grow: 1;
  overflow-y: auto;
  padding: 0;
  scrollbar-width: none;
  -ms-overflow-style: none;

  &::-webkit-scrollbar {
    display: none;
  }
}

.detail-info-card {
  background: #ffffff;
  padding: 20px;
  border-radius: 20px 20px 0 0;
  margin-top: 20px;
  position: relative;
  box-shadow: 0 -5px 15px rgba(0, 0, 0, 0.05);
}

.detail-name {
  font-size: 1.5rem;
  font-weight: 700;
  margin-bottom: 10px;
  color: #2c2c2c;
}

.detail-tags {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
  margin-bottom: 15px;

  .tag {
    background-color: #fff8e1;
    color: #666666;
    font-size: 0.8rem;
    padding: 4px 10px;
    border-radius: 15px;
  }
}

.detail-tabs {
  display: flex;
  background-color: #ffffff;
  padding: 0 18px;
  border-bottom: 1px solid #f0f0f0;
  position: sticky;
  top: 0;
  z-index: 10;

  .tab-link {
    padding: 12px 16px;
    cursor: pointer;
    border: none;
    background: transparent;
    font-size: 0.95rem;
    font-weight: 600;
    color: #666666;
    position: relative;
    transition: color 0.2s;

    &.active {
      color: #2c2c2c;

      &::after {
        content: '';
        position: absolute;
        bottom: 0;
        left: 50%;
        transform: translateX(-50%);
        width: 40%;
        height: 3px;
        background-color: #ffc300;
        border-radius: 2px;
      }
    }
  }
}

.tab-content {
  display: none;
  padding: 18px;
  animation: fadeIn 0.3s;

  &.active {
    display: block;
  }
}

.review-item,
.service-item {
  background: #ffffff;
  padding: 15px;
  border-radius: 10px;
  margin-bottom: 12px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.06);
  display: flex;
  align-items: flex-start;
  gap: 12px;
}

.service-item::before {
  font-family: 'Font Awesome 5 Free';
  font-weight: 900;
  content: '\f00c';
  color: #ffc300;
  background-color: #fff8e1;
  border-radius: 50%;
  width: 24px;
  height: 24px;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  flex-shrink: 0;
  font-size: 0.8rem;
}

.review-item::before {
  font-family: 'Font Awesome 5 Free';
  font-weight: 900;
  content: '\f075';
  color: #ffc300;
  background-color: #fff8e1;
  border-radius: 50%;
  width: 24px;
  height: 24px;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  flex-shrink: 0;
  font-size: 0.8rem;
}

.review-item p,
.service-item p {
  margin: 0;
  color: #2c2c2c;
  line-height: 1.5;
  font-size: 0.9rem;
}

.description-item {
  background: #f8f9fa !important;
  box-shadow: none !important;
  padding-left: 15px !important;
  align-items: flex-start;

  div {
    width: 100%;
  }

  .title {
    font-weight: 600;
    color: #2c2c2c;
    margin-bottom: 5px;
  }

  .text {
    line-height: 1.6;
    color: #2c2c2c;
  }
}

.image-item {
  margin-bottom: 15px;

  h5 {
    font-size: 0.9rem;
    color: #999999;
    margin-bottom: 8px;
    font-weight: 600;
  }

  .image-placeholder {
    background-color: #f7f7f7;
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #2c2c2c;
    height: 180px;
    width: 100%;
    font-size: 1rem;
    border: 1px dashed #ddd;
  }
}

.detail-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 12px 16px;
  background: #ffffff;
  border-top: 1px solid #f0f0f0;
  flex-shrink: 0;

  .price-info .price {
    font-size: 1.4rem;
    font-weight: 700;
    color: #ff4a4a;
  }

  .order-btn {
    background: linear-gradient(135deg, #ffc300 0, #e6b000 100%);
    color: #ffffff;
    border: none;
    padding: 12px 24px;
    border-radius: 50px;
    font-size: 1rem;
    font-weight: 600;
    cursor: pointer;
    transition: all 0.25s cubic-bezier(0.25, 0.46, 0.45, 0.94);
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.07);
    position: relative;
    overflow: hidden;

    &::before {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      width: 0;
      height: 0;
      background: rgba(255, 255, 255, 0.3);
      border-radius: 50%;
      transform: translate(-50%, -50%);
      transition: all 0.25s cubic-bezier(0.25, 0.46, 0.45, 0.94);
    }

    &:hover {
      background: linear-gradient(135deg, #e6b000 0, #cc9900 100%);
      transform: translateY(-2px) scale(1.05);
      box-shadow: 0 10px 15px rgba(0, 0, 0, 0.1);
    }

    &:active::before {
      width: 100px;
      height: 100px;
      animation: ripple 0.35s cubic-bezier(0.25, 0.46, 0.45, 0.94);
    }

    &:active {
      transform: translateY(0) scale(1.02);
      box-shadow: 0 4px 6px rgba(0, 0, 0, 0.07);
    }
  }
}

.modal-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.6);
  backdrop-filter: blur(8px);
  z-index: 2000;
  display: none;
  justify-content: center;
  align-items: center;
  animation: fadeIn 0.25s cubic-bezier(0.25, 0.46, 0.45, 0.94);
}

.modal-content {
  background: linear-gradient(135deg, #ffffff 0, #fafbfc 100%);
  padding: 24px;
  border-radius: 16px;
  width: 85%;
  max-width: 400px;
  box-shadow: 0 20px 25px rgba(0, 0, 0, 0.15);
  text-align: center;
  border: 1px solid #f8f8f8;
  position: relative;
  overflow: hidden;
  animation: slideInRight 0.25s cubic-bezier(0.34, 1.56, 0.64, 1);

  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 4px;
    background: linear-gradient(90deg, #ffc300, #ff6b35);
  }

  h3 {
    margin-bottom: 16px;
    color: #2c2c2c;
    font-size: 1.2rem;
    font-weight: 700;
  }
}

.modal-content-tags {
  margin-bottom: 15px;
}

.modal-tags-title {
  font-size: 1rem;
  font-weight: 600;
  color: #2c2c2c;
  margin-bottom: 10px;
  text-align: left;
}

.modal-tags-wrapper {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
  justify-content: flex-start;
}

.content-tag-btn {
  background: linear-gradient(135deg, #f0f0f0 0, #e8e8e8 100%);
  color: #2c2c2c;
  font-size: 0.85rem;
  padding: 8px 12px;
  border-radius: 50px;
  border: 1px solid #e0e0e0;
  cursor: pointer;
  transition: all 0.25s cubic-bezier(0.25, 0.46, 0.45, 0.94);
  white-space: nowrap;
  position: relative;
  overflow: hidden;

  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100%;
    background: linear-gradient(90deg, transparent, rgba(255, 195, 0, 0.2), transparent);
    transition: left 0.35s cubic-bezier(0.25, 0.46, 0.45, 0.94);
  }

  &:hover {
    background: linear-gradient(135deg, #fff8e1 0, #fff3cc 100%);
    border-color: #ffc300;
    color: #111111;
    transform: translateY(-2px) scale(1.05);
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.07);

    &::before {
      left: 100%;
    }
  }

  &:active {
    transform: translateY(-1px) scale(1.02);
    box-shadow: 0 1px 2px rgba(0, 0, 0, 0.05);
  }
}

.modal-content textarea {
  width: 100%;
  height: 80px;
  border: 2px solid #f0f0f0;
  border-radius: 8px;
  padding: 12px;
  margin-bottom: 20px;
  font-size: 0.9rem;
  resize: none;
  outline: none;
  transition: all 0.25s cubic-bezier(0.25, 0.46, 0.45, 0.94);
  background: #ffffff;
  font-family: inherit;

  &:focus {
    border-color: #ffc300;
    box-shadow: 0 0 0 3px #fff3cc;
    transform: translateY(-1px);
  }
}

.modal-buttons {
  display: flex;
  justify-content: space-between;
  gap: 12px;

  button {
    flex: 1;
    padding: 12px 16px;
    border-radius: 8px;
    border: none;
    font-size: 0.95rem;
    font-weight: 600;
    cursor: pointer;
    transition: all 0.25s cubic-bezier(0.25, 0.46, 0.45, 0.94);
    position: relative;
    overflow: hidden;

    &::before {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      width: 0;
      height: 0;
      background: rgba(255, 255, 255, 0.3);
      border-radius: 50%;
      transform: translate(-50%, -50%);
      transition: all 0.25s cubic-bezier(0.25, 0.46, 0.45, 0.94);
    }

    &:active::before {
      width: 100px;
      height: 100px;
      animation: ripple 0.35s cubic-bezier(0.25, 0.46, 0.45, 0.94);
    }
  }

  .modal-btn-confirm {
    background: linear-gradient(135deg, #ffc300 0, #e6b000 100%);
    color: #ffffff;
    box-shadow: 0 1px 2px rgba(0, 0, 0, 0.05);

    &:hover {
      background: linear-gradient(135deg, #e6b000 0, #cc9900 100%);
      transform: translateY(-1px);
      box-shadow: 0 4px 6px rgba(0, 0, 0, 0.07);
    }
  }

  .modal-btn-cancel {
    background: linear-gradient(135deg, #f5f5f5 0, #e8e8e8 100%);
    color: #999999;
    box-shadow: 0 1px 2px rgba(0, 0, 0, 0.05);

    &:hover {
      background: linear-gradient(135deg, #e8e8e8 0, #dddddd 100%);
      color: #333333;
      transform: translateY(-1px);
      box-shadow: 0 4px 6px rgba(0, 0, 0, 0.07);
    }
  }
}

.empty-state {
  text-align: center;
  color: #999999;
  padding: 40px 20px;
  line-height: 1.6;
}

// 快速下单区域
.quick-order-section {
  background: #ffffff;
  padding: 16px;
  border-radius: 12px;
  margin-top: 16px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.06);
  border: 1px solid #f0f0f0;
}

.quick-order-title {
  font-size: 1rem;
  font-weight: 700;
  color: #2c2c2c;
  margin: 0 0 12px 0;
  display: flex;
  align-items: center;
  gap: 8px;

  &::before {
    content: '';
    display: block;
    width: 4px;
    height: 16px;
    background: linear-gradient(135deg, #ffc300 0, #ff6b35 100%);
    border-radius: 6px;
  }
}

.quick-order-buttons {
  display: flex;
  gap: 10px;
  flex-wrap: wrap;
}

.quick-order-btn {
  flex: 1;
  min-width: calc(33.333% - 7px);
  padding: 12px 16px;
  background: linear-gradient(135deg, #fff9e6 0, #fffbf0 100%);
  border: 2px solid #ffc300;
  border-radius: 12px;
  font-size: 0.9rem;
  font-weight: 600;
  color: #d98600;
  cursor: pointer;
  transition: all 0.25s cubic-bezier(0.25, 0.46, 0.45, 0.94);
  position: relative;
  overflow: hidden;

  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100%;
    background: linear-gradient(90deg, transparent, rgba(255, 195, 0, 0.2), transparent);
    transition: left 0.35s cubic-bezier(0.25, 0.46, 0.45, 0.94);
  }

  &:hover {
    background: linear-gradient(135deg, #ffc300 0, #e6b000 100%);
    color: #ffffff;
    transform: translateY(-2px);
    box-shadow: 0 4px 12px rgba(255, 195, 0, 0.3);

    &::before {
      left: 100%;
    }
  }

  &:active {
    transform: translateY(0);
    box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);
  }
}

@media (max-width: 480px) {
  .quick-order-btn {
    min-width: 100%;
    white-space: normal;
    word-wrap: break-word;
    text-align: center;
    line-height: 1.3;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes slideInRight {
  from {
    opacity: 0;
    transform: translateX(30px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes ripple {
  0% {
    transform: scale(0);
    opacity: 1;
  }
  100% {
    transform: scale(4);
    opacity: 0;
  }
}

// 深色模式
body.dark-mode {
  .app-view {
    background-color: #1a1a1a;
  }

  .app-header {
    background: linear-gradient(135deg, #2a2a2a 0, #1f1f1f 100%);
    border-bottom-color: #333;

    .title {
      color: #e0e0e0;
    }
  }

  .detail-info-card,
  .detail-tabs {
    background: linear-gradient(135deg, #2a2a2a 0, #1f1f1f 100%);
    border-color: #333;
  }

  .detail-name {
    color: #e0e0e0;
  }

  .tab-content {
    background: linear-gradient(135deg, #2a2a2a 0, #1f1f1f 100%);
    border-color: #333;
  }

  .service-item,
  .review-item {
    background: #2a2a2a;
    border-color: #333;

    p {
      color: #e0e0e0;
    }
  }

  .description-item {
    background: #1a1a1a !important;
    color: #e0e0e0;

    .title,
    .text {
      color: #e0e0e0;
    }
  }

  .quick-order-section {
    background: #2a2a2a;
    border-color: #333;

    .quick-order-title {
      color: #e0e0e0;
    }
  }

  .detail-footer {
    background: linear-gradient(135deg, #2a2a2a 0, #1f1f1f 100%);
    border-top-color: #333;
  }
}
