
.app-view {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  background-color: #f8f9fa;
  position: absolute;
  top: 0;
  left: 0;
}

.app-header {
  background: linear-gradient(135deg, #ffffff 0, #fafbfc 100%);
  padding: 35px 16px 12px 16px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #f0f0f0;
  flex-shrink: 0;

  .title {
    font-size: 1.3rem;
    font-weight: 700;
    display: flex;
    align-items: center;
    gap: 8px;
    color: #2c2c2c;

    i {
      color: #ffc300;
    }
  }
}

.app-content {
  flex-grow: 1;
  overflow-y: auto;
  padding: 16px;
  scrollbar-width: none;
  -ms-overflow-style: none;

  &::-webkit-scrollbar {
    display: none;
  }
}

.card {
  background: linear-gradient(135deg, #ffffff 0, #fffef8 100%);
  border-radius: 16px;
  padding: 20px;
  margin-bottom: 12px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.06);
  border: 1px solid rgba(255, 195, 0, 0.1);

  .card-title {
    font-size: 16px;
    font-weight: 700;
    margin-bottom: 14px;
    display: flex;
    align-items: center;
    gap: 8px;

    i {
      color: #ffc300;
    }
  }
}

.empty-state {
  text-align: center;
  padding: 40px 20px;
  color: #999999;
  line-height: 1.6;
}

.history-card {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: white;
  border-radius: 16px;
  padding: 16px;
  margin-bottom: 12px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.06);
  cursor: pointer;
  transition: all 0.4s;
  border: 1px solid #f8f8f8;

  &:hover {
    transform: translateY(-4px);
    box-shadow: 0 12px 40px rgba(255, 195, 0, 0.25);
  }

  .history-info {
    flex-grow: 1;
    margin-right: 16px;

    .history-title {
      font-size: 17px;
      font-weight: 700;
      margin-bottom: 6px;
      color: #2c2c2c;
    }

    .history-details {
      display: flex;
      flex-direction: column;
      gap: 4px;

      .detail-item {
        display: flex;
        align-items: center;
        gap: 6px;
        font-size: 13px;
        color: #999999;

        i {
          width: 14px;
          text-align: center;
        }

        .status-badge {
          margin-left: 4px;
        }
      }
    }
  }

  .history-price-section {
    text-align: right;
    flex-shrink: 0;

    .history-price {
      font-size: 28px;
      font-weight: 900;
      background: linear-gradient(135deg, #FF6B6B 0%, #FF5252 100%);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      margin-bottom: 10px;

      &::before {
        content: '¥';
        font-size: 20px;
      }
    }

    .quick-reorder-btn {
      display: flex;
      align-items: center;
      gap: 4px;
      padding: 6px 12px;
      background: linear-gradient(135deg, #fff9e6, #fff);
      border: 1px solid #ffc300;
      border-radius: 50px;
      font-size: 12px;
      font-weight: 600;
      color: #d98600;
      transition: all 0.25s;

      &:hover {
        background: linear-gradient(135deg, #ffc300, #e6b000);
        color: white;
      }

      i {
        font-size: 11px;
      }
    }
  }
}

.reorder-modal-overlay {
  position: fixed;
  inset: 0;
  background: rgba(0, 0, 0, 0.6);
  backdrop-filter: blur(8px);
  z-index: 2000;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 16px;
}

.reorder-modal-content {
  background: white;
  border-radius: 20px;
  width: 100%;
  max-width: 450px;
  max-height: 90vh;
  overflow-y: auto;
  box-shadow: 0 12px 40px rgba(255, 195, 0, 0.25);
  position: relative;

  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 4px;
    background: linear-gradient(135deg, #ffc300, #e6b000);
    border-radius: 20px 20px 0 0;
  }

  .modal-header {
    padding: 24px 24px 16px 24px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid #f0f0f0;

    .modal-title {
      font-size: 20px;
      font-weight: 700;
      display: flex;
      align-items: center;
      gap: 12px;
      color: #2c2c2c;

      i {
        color: #ffc300;
      }
    }

    .btn-close {
      padding: 8px 16px;
      background: #f5f5f5;
      border: none;
      border-radius: 8px;
      font-size: 14px;
      font-weight: 600;
      cursor: pointer;
      transition: all 0.25s;
      color: #666666;

      &:hover {
        background: #e0e0e0;
      }

      i {
        margin-right: 4px;
      }
    }
  }

  .modal-body {
    padding: 20px 24px;
    overflow-y: auto;
  }

  .modal-footer {
    padding: 16px 24px 24px 24px;
    display: flex;
    gap: 12px;

    .btn {
      flex: 1;
      padding: 14px;
      border-radius: 12px;
      border: none;
      font-weight: 600;
      cursor: pointer;
      transition: all 0.25s;
      display: flex;
      align-items: center;
      justify-content: center;
      gap: 8px;

      &.btn-primary {
        background: linear-gradient(135deg, #ffc300 0, #e6b000 100%);
        color: white;
        box-shadow: 0 4px 12px rgba(255, 195, 0, 0.2);

        &:hover {
          background: linear-gradient(135deg, #e6b000 0, #cc9900 100%);
          transform: translateY(-2px);
        }
      }

      &.btn-secondary {
        background: #f5f5f5;
        color: #666666;

        &:hover {
          background: #e0e0e0;
        }
      }
    }
  }
}

.order-info-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 12px;
  margin-bottom: 20px;

  .info-box {
    background: #fafafa;
    padding: 16px;
    border-radius: 16px;
    border: 1px solid #f0f0f0;

    .info-label {
      font-size: 12px;
      color: #999999;
      margin-bottom: 6px;
    }

    .info-value {
      font-size: 16px;
      font-weight: 700;
      color: #2c2c2c;

      &.price {
        font-size: 20px;
        color: #FF6B6B;
      }
    }
  }
}

.info-section {
  background: #fafafa;
  padding: 16px;
  border-radius: 16px;
  margin-bottom: 16px;
  border: 1px solid #f0f0f0;

  .section-title {
    font-size: 16px;
    font-weight: 700;
    margin-bottom: 12px;
    display: flex;
    align-items: center;
    gap: 8px;
    color: #2c2c2c;

    i {
      color: #ffc300;
    }
  }

  .section-content {
    font-size: 14px;
    color: #666666;
    line-height: 1.7;
  }

  .section-grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 12px;
  }

  .section-item {
    .section-item-label {
      font-size: 12px;
      color: #999999;
      margin-bottom: 4px;
    }

    .section-item-value {
      font-size: 14px;
      font-weight: 700;
      color: #2c2c2c;
    }
  }
}

.modal-overlay {
  position: fixed;
  inset: 0;
  background: rgba(0, 0, 0, 0.6);
  backdrop-filter: blur(8px);
  z-index: 2000;
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal-content {
  background: white;
  padding: 28px;
  border-radius: 20px;
  width: 90%;
  max-width: 450px;
  box-shadow: 0 12px 40px rgba(255, 195, 0, 0.25);
  position: relative;

  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 4px;
    background: linear-gradient(135deg, #ffc300 0, #e6b000 100%);
    border-radius: 20px 20px 0 0;
  }

  h3 {
    margin-bottom: 20px;
    text-align: center;
    font-size: 20px;
    font-weight: 700;
  }

  .modal-tags-section {
    margin-bottom: 16px;

    h4 {
      font-size: 14px;
      font-weight: 600;
      margin-bottom: 10px;
      text-align: left;
    }
  }

  .modal-tags-wrapper {
    display: flex;
    flex-wrap: wrap;
    gap: 8px;
  }

  .content-tag-btn {
    background: linear-gradient(135deg, #f0f0f0, #e8e8e8);
    color: #2c2c2c;
    font-size: 13px;
    padding: 8px 14px;
    border-radius: 50px;
    border: 1.5px solid #e0e0e0;
    cursor: pointer;
    transition: all 0.3s;

    &:hover {
      background: linear-gradient(135deg, #ffc300, #e6b000);
      color: white;
      transform: translateY(-2px);
    }
  }

  textarea {
    width: 100%;
    height: 100px;
    border: 2px solid #EEEEEE;
    border-radius: 12px;
    padding: 12px;
    margin-bottom: 20px;
    font-size: 14px;
    resize: none;
    transition: all 0.3s;
    font-family: inherit;

    &:focus {
      outline: none;
      border-color: #ffc300;
      box-shadow: 0 0 0 3px #fff3cc;
    }
  }

  .modal-buttons {
    display: flex;
    gap: 12px;

    button {
      flex: 1;
      padding: 14px;
      border-radius: 12px;
      border: none;
      font-weight: 600;
      cursor: pointer;
      transition: all 0.3s;
    }

    .modal-btn-confirm {
      background: linear-gradient(135deg, #ffc300 0, #e6b000 100%);
      color: white;
      box-shadow: 0 4px 12px rgba(255, 195, 0, 0.2);

      &:hover {
        transform: translateY(-2px);
      }
    }

    .modal-btn-cancel {
      background: #f5f5f5;
      color: #666666;

      &:hover {
        background: #e0e0e0;
      }
    }
  }
}

.nav-bar {
  display: flex;
  border-top: 1px solid #f0f0f0;
  background: linear-gradient(135deg, #ffffff 0, #fafbfc 100%);
  padding: 8px 0;
  flex-shrink: 0;

  .nav-item {
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    color: #666666;
    font-size: 0.8rem;
    padding: 4px 0;
    cursor: pointer;
    transition: all 0.25s;
    border-radius: 8px;
    margin: 0 4px;

    &.active {
      color: #2c2c2c;

      i {
        color: #ffc300;
        transform: scale(1.15) translateY(-2px);
      }
    }

    &:hover:not(.active) {
      color: #333333;
      transform: translateY(-1px);
    }

    i {
      font-size: 1.25rem;
      margin-bottom: 4px;
      transition: all 0.25s;
    }
  }
}

@media (max-width: 480px) {
  .order-info-grid {
    grid-template-columns: 1fr;
  }

  .info-section {
    .section-grid {
      grid-template-columns: 1fr;
    }
  }

  .history-card {
    flex-direction: column;
    align-items: flex-start;

    .history-info {
      margin-right: 0;
      margin-bottom: 12px;
      width: 100%;
    }

    .history-price-section {
      width: 100%;
      text-align: left;

      .history-price {
        font-size: 24px;
      }
    }
  }
}

// 深色模式
body.dark-mode {
  .app-view {
    background-color: #1a1a1a;
  }

  .app-header {
    background: linear-gradient(135deg, #2a2a2a 0, #1f1f1f 100%);
    border-bottom-color: #333;

    .title {
      color: #e0e0e0;
    }
  }

  .card {
    background: linear-gradient(135deg, #2a2a2a 0, #1f1f1f 100%);
    border-color: #333;
  }

  .history-card {
    background: #2a2a2a;
    border-color: #333;

    .history-title {
      color: #e0e0e0;
    }
  }

  .reorder-modal-content {
    background: #2a2a2a;
    border-color: #333;
  }

  .modal-header {
    border-bottom-color: #333;

    .modal-title {
      color: #e0e0e0;
    }
  }

  .order-info-grid .info-box {
    background: #333;
    border-color: #444;
  }

  .info-section {
    background: #333;
    border-color: #444;

    .section-title {
      color: #e0e0e0;
    }
  }

  .section-item-value {
    color: #e0e0e0;
  }

  .modal-content {
    background: #2a2a2a;

    h3 {
      color: #e0e0e0;
    }
  }

  .nav-bar {
    background: linear-gradient(135deg, #2a2a2a 0, #1f1f1f 100%);
    border-top-color: #333;
  }
}
